<template>
  <v-app-bar app :color="color" dark>
    <v-app-bar-title>
      <v-icon x-large>mdi-caravan</v-icon> Caravan Kalender</v-app-bar-title
    >
    <v-spacer></v-spacer>
    <v-btn
      v-if="user === 'remko.vroomans@gmail.com'"
      class="mx-1"
      plain
      dark
      @click="getProfile"
    >
      Profiel
    </v-btn>
    <v-btn v-if="user" class="mx-1" plain dark @click="logout">
      <v-icon>mdi-exit-to-app</v-icon>
    </v-btn>
    <v-dialog v-model="profileDialog" width="500">
      <v-card class="mx-auto">
        <v-card-title class="font-weight-light">{{
          profile.name
        }}</v-card-title>
        <v-card-subtitle>{{ user }}</v-card-subtitle>
        <v-list>
          <v-list-item-group v-model="model">
            <v-list-item v-for="(email, i) in profile.emails" :key="i" inactive>
              <v-list-item-icon>
                <v-icon>mdi-email</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>Email {{ i + 1 }}</v-list-item-title>
                <v-list-item-subtitle v-text="email"> </v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
            <v-list-item>
              <v-list-item-icon>
                <v-icon>mdi-lock</v-icon>
              </v-list-item-icon>
              <v-list-item-title>Wijzig wachtwoord</v-list-item-title>
            </v-list-item>
          </v-list-item-group>
        </v-list>
        <v-btn :color="color" dark class="ma-5"> Opslaan </v-btn>
        <v-btn text color="secondary" @click.stop="profileDialog = false">
          Annuleer
        </v-btn>
      </v-card>
    </v-dialog>
  </v-app-bar>
</template>

<script>
import { db } from "@/main";
export default {
  name: "navbar",
  props: ["user", "color"],
  data: () => ({
    profileDialog: false,
    profile: {},
    emails: [],
    model: 1,
  }),

  methods: {
    logout() {
      this.$emit("logout");
    },

    async getProfile() {
      await db
        .collection("users")
        .get()
        .then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            if (doc.data().emails.includes(this.user)) {
              this.profile = doc.data();
              this.profile.id = doc.data().id;
              this.emails = doc.data().emails;
              this.profileDialog = true;
            }
          });
        })
        .catch((error) => {
          console.log("Error getting documents: ", error);
        });
    },
  },
};
</script>