<template>
  <v-app>
    <navbar :user="currentUser" :name="name" :color="color" @logout="logout" />

    <v-main>
      <router-view
        @login="login"
        :color="color"
        :user="name"
        :mail="currentUser"
        :first="first"
        @changePass="changePass"
        @firstCancel="firstCancel"
      />
    </v-main>
  </v-app>
</template>

<script>
import Navbar from "./components/Navbar.vue";
import firebase from "firebase";
import { db } from "@/main";

export default {
  components: { Navbar },
  name: "App",

  data: () => {
    return {
      currentUser: null,
      color: "primary",
      name: "",
      first: false,
      firstMails: [],
    };
  },

  methods: {
    async loadUser() {
      await db
        .collection("users")
        .get()
        .then((querySnapshot) => {
          querySnapshot.docs.forEach((e) => {
            if (e.id === "first") {
              this.firstMails = e.data().emails;
              if (!this.firstMails.includes(this.currentUser)) {
                this.first = true;
                this.firstMails.push(this.currentUser);
                db.collection("users").doc("first").update({
                  emails: this.firstMails,
                });
              } else this.first = false;
            }
          });

          querySnapshot.forEach((doc) => {
            if (doc.data().emails.includes(this.currentUser)) {
              this.color = doc.data().color;
              this.name = doc.data().name;
            }
          });
        })
        .catch((error) => {
          console.log("Error getting documents: ", error);
        });
    },

    login(e, mail, password) {
      firebase
        .auth()
        .signInWithEmailAndPassword(mail, password)
        .then(
          (u) => {
            const user = u.user;

            this.currentUser = user.email;
            this.loadUser();
            this.$router.push("/kalender");
          },
          (err) => {
            alert("In de login methode: " + err.message);
          }
        );
      e.preventDefault();
    },

    logout() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.currentUser = null;
          this.color = "primary";
          this.$router.push("/");
        });
    },

    changePass(e, pass) {
      const user = firebase.auth().currentUser;

      user
        .updatePassword(pass)
        .then(function () {
          this.first = false;
          this.logout();
        })
        .catch(function (error) {
          alert(error);
          return;
        });

      e.preventDefault();
    },

    firstCancel() {
      this.first = false;
    },
  },
};
</script>