  <template>
  <v-container>
    <v-dialog
      v-model="first"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card width="400">
        <v-card-title>Verander Wachtwoord</v-card-title>
        <v-card-subtitle
          >Omdat je voor het eerst inlogt kun je je wachtwoord aanpassen. Daarna
          moet je opnieuw inloggen met je nieuwe wachtwoord.</v-card-subtitle
        >
        <v-form ref="form" class="pa-4">
          <v-text-field
            v-model="password"
            :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
            :type="show ? 'text' : 'password'"
            name="input-10-1"
            label="Nieuw Wachtwoord"
            @click:append="show = !show"
            prepend-icon="mdi-lock"
          ></v-text-field>

          <v-btn :color="color" class="mr-4" @click="changePass">
            Pas aan
          </v-btn>
          <v-btn text color="secondary" @click.stop="firstCancel">
            Zelfde laten
          </v-btn>
        </v-form>
      </v-card>
    </v-dialog>
    <v-card Class="mx-5">
      <v-card-title class="font-weight-light"
        >{{ user }}
        <v-spacer></v-spacer>
        <v-btn :color="color" fab dark class="my-2" @click="newEvent">
          <v-icon>mdi-plus</v-icon>
        </v-btn>
      </v-card-title>
      <v-row class="fill-height">
        <v-col>
          <v-sheet height="64">
            <v-toolbar flat>
              <v-btn
                outlined
                class="mr-4"
                color="grey darken-2"
                @click="setToday"
              >
                Vandaag
              </v-btn>
              <v-btn fab text small color="grey darken-2" @click="prev">
                <v-icon small> mdi-chevron-left </v-icon>
              </v-btn>
              <v-btn fab text small color="grey darken-2" @click="next">
                <v-icon small> mdi-chevron-right </v-icon>
              </v-btn>
              <v-toolbar-title v-if="$refs.calendar">
                {{ $refs.calendar.title }}
              </v-toolbar-title>
            </v-toolbar>
          </v-sheet>

          <v-dialog v-model="dialog" width="500">
            <v-card class="mx-auto">
              <v-card-title>{{ user }}</v-card-title>
              <v-card flat class="d-flex justify-center">
                <v-card-actions>
                  <v-date-picker
                    v-model="dates"
                    range
                    elevation="10"
                    :color="color"
                    first-day-of-week="1"
                    locale="nl-NL"
                    selected-items-text="Opslaan?"
                  ></v-date-picker>
                </v-card-actions>
              </v-card>

              <v-btn
                v-if="selectedEvent.id && user === selectedEvent.name"
                :color="color"
                dark
                class="ma-5"
                @click="updateEvent"
              >
                Bijwerken
              </v-btn>
              <v-btn v-else :color="color" dark class="ma-5" @click="saveEvent">
                Opslaan
              </v-btn>
              <v-btn text color="secondary" @click.stop="dialog = false">
                Annuleer
              </v-btn>
            </v-card>
          </v-dialog>
          <v-sheet height="500" class="px-3">
            <v-calendar
              ref="calendar"
              v-model="focus"
              :color="color"
              :events="events"
              :event-color="getEventColor"
              :type="type"
              @click:event="showEvent"
              locale="nl-NL"
              :weekdays="weekdays"
            ></v-calendar>
            <v-menu
              v-model="selectedOpen"
              :close-on-content-click="false"
              :activator="selectedElement"
              max-width="350px"
            >
              <v-card color="grey lighten-4" min-width="350px" flat>
                <v-toolbar :color="selectedEvent.color" dark>
                  <v-toolbar-title
                    v-html="selectedEvent.name"
                  ></v-toolbar-title>
                  <v-spacer></v-spacer>
                  <v-btn
                    v-if="user === selectedEvent.name"
                    @click="changeEvent"
                    icon
                  >
                    <v-icon>mdi-calendar</v-icon>
                  </v-btn>
                  <v-btn
                    v-if="
                      user === selectedEvent.name ||
                      mail === 'remko.vroomans@gmail.com'
                    "
                    @click="delEvent = true"
                    icon
                  >
                    <v-icon>mdi-delete</v-icon>
                  </v-btn>
                </v-toolbar>
                <v-dialog v-model="delEvent" width="400">
                  <v-card>
                    <v-card-title>Weet je het zeker?</v-card-title>
                    <v-card-actions class="d-flex justify-center">
                      <v-btn
                        :color="color"
                        dark
                        class="ma-5"
                        @click="deleteEvent(selectedEvent.id)"
                        >Verwijderen</v-btn
                      >
                      <v-btn
                        text
                        color="secondary"
                        @click.stop="delEvent = false"
                      >
                        Annuleer
                      </v-btn>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
                <v-card-text>
                  <span>{{
                    `Gereserveerd van ${selectedEvent.start} tot ${selectedEvent.end}.`
                  }}</span>
                </v-card-text>
                <v-card-actions>
                  <v-btn text color="secondary" @click="selectedOpen = false">
                    Sluiten
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-menu>
          </v-sheet>
        </v-col>
      </v-row>
    </v-card>
  </v-container>
</template>


<script>
import { db } from "@/main";

export default {
  name: "calendar",

  props: ["color", "user", "mail", "first"],

  data: () => ({
    username: "",
    password: "",
    details: null,
    start: null,
    end: null,
    focus: "",
    type: "month",
    selectedEvent: {},
    selectedElement: null,
    selectedOpen: false,
    events: [],
    dialog: false,
    delEvent: false,
    dates: [],
    weekdays: [1, 2, 3, 4, 5, 6, 0],

    show: false,
  }),

  mounted() {
    this.getEvents();
  },

  methods: {
    newEvent() {
      this.selectedEvent = {};
      this.dates = [];
      this.dialog = true;
    },
    getEventColor(event) {
      return event.color;
    },
    setToday() {
      this.focus = "";
    },
    prev() {
      this.$refs.calendar.prev();
    },
    next() {
      this.$refs.calendar.next();
    },
    showEvent({ nativeEvent, event }) {
      const open = () => {
        this.selectedEvent = event;
        this.selectedElement = nativeEvent.target;
        setTimeout(() => {
          this.selectedOpen = true;
        }, 10);
      };

      if (this.selectedOpen) {
        this.selectedOpen = false;
        setTimeout(open, 10);
      } else {
        open();
      }

      nativeEvent.stopPropagation();
    },
    changeEvent() {
      this.dates[0] = this.selectedEvent.start;
      this.dates[1] = this.selectedEvent.end;
      this.dialog = true;
    },

    async getEvents() {
      let snapshot = await db.collection("reserveringen").get();
      let events = [];

      snapshot.forEach((doc) => {
        let appData = doc.data();
        appData.id = doc.id;
        events.push(appData);
      });
      this.events = events;
    },

    async deleteEvent(id) {
      await db.collection("reserveringen").doc(id).delete();
      this.selectedOpen = false;
      this.getEvents();
    },

    async updateEvent() {
      const id = this.selectedEvent.id;
      this.start = this.dates[0];
      this.end = this.dates[1];

      await db.collection("reserveringen").doc(id).update({
        start: this.start,
        end: this.end,
      });
      this.selectedOpen = false;
      this.selectedEvent = {};
      this.start = "";
      this.end = "";
      this.dates = [];
      this.dialog = false;
      this.getEvents();
    },

    async saveEvent() {
      this.start = this.dates[0];
      this.end = this.dates[1];

      await db.collection("reserveringen").add({
        name: this.user,
        start: this.start,
        end: this.end,
        color: this.color,
      });
      this.getEvents();
      this.start = "";
      this.end = "";
      this.dates = [];
      this.dialog = false;
    },

    changePass(e) {
      this.$emit("changePass", e, this.password);
    },

    firstCancel(e) {
      this.$emit("firstCancel", e);
    },
  },
};
</script>
