  <template>
  <v-container class="d-flex justify-center mt-5" width="600">
    <v-card width="400">
      <v-card-title>Login</v-card-title>
      <v-form ref="form" class="pa-4">
        <v-text-field
          v-model="email"
          label="E-mail"
          required
          prepend-icon="mdi-email"
        ></v-text-field>
        <v-text-field
          v-model="password"
          :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
          :type="show ? 'text' : 'password'"
          name="input-10-1"
          label="Wachtwoord"
          @click:append="show = !show"
          prepend-icon="mdi-lock"
        ></v-text-field>

        <v-btn color="primary" class="mr-4" @click="login"> Inloggen </v-btn>
      </v-form>
    </v-card>
  </v-container>
</template>



<script>
export default {
  name: "login",

  data: () => {
    return {
      email: "",
      password: "",
      show: false,
      dialog: false,
    };
  },

  methods: {
    login(e) {
      this.$emit("login", e, this.email, this.password);
    },
  },
};
</script>